.App {
  text-align: center;
  background-color: #0e0c38;
  padding: 0;
  margin: 0;
}

.container {
  max-width: 1380px;
  margin: 0 auto;
  padding: 0px 15px;
  width: 100%;
}

.App-link {
  color: #61dafb;
}

a {
  text-decoration: none;
  color: white;
}

ul {
  list-style-type: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

h1 {
  font-size: 110px;
  line-height: 100px;
  font-weight: bold;
}

h2 {
  font-size: 48px;
  font-weight: 500;
}

h3 {
  font-size: 42px;
  font-weight: 500;
}

h5 {
  font-size: 30px;
}
p {
  font-size: 24px;
  font-weight: 200;
}
a {
  text-decoration: none;
  display: inline-block;
}
ul {
  list-style: none;
}

