.Header_navList__T3Sf0 li span::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 140%;
  -webkit-transform: scaleY(0);
          transform: scaleY(0);
  -webkit-transform-origin: top center;
          transform-origin: top center;
  background: red;
  z-index: -1;
  transition: -webkit-transform 0.38s;
  transition: transform 0.38s;
  transition: transform 0.38s, -webkit-transform 0.38s;
}

.Header_navList__T3Sf0 li span:hover::after {
  -webkit-transform: scaleY(1);
          transform: scaleY(1);
}

.Header_navList__T3Sf0 li:last-child span::after {
  content: "";
  background: transparent;
}

.Header_navList__T3Sf0 li:last-child span:hover {
  border: 2px solid red;
